import React, { Component } from 'react'
import Modal from '../../components/UI/Model/Modal'
import Aux from '../../hoc/Aux'


const withErrorHandler =(WrappedComponent,axios)=>{
    return class extends Component {
        state ={
            error:null,

        }
     componentWillMount(){
           this.reqIntercepters =axios.interceptors.request.use(req=>{
                this.setState({error:null})
                return req
            })
            this.resIntercepters=axios.interceptors.response.use(res=>res,error=>{
        this.setState({error:error})
            })
            
        }
        componentWillUnmount(){
            axios.interceptors.request.eject(this.reqIntercepters)
            axios.interceptors.response.eject(this.resIntercepters)
        }
        errorConfirmedHandler=()=>{
            this.setState({error:null})

        }
        render (){
            return (
                <Aux>
                    <Modal show={this.state.error} modalClosed={this.errorConfirmedHandler}>{this.state.error ? this.state.error.message : null}</Modal>
                    <WrappedComponent  {...this.props}/>
                </Aux>
            )
        }
    } 
}

export default withErrorHandler