import React,{Component} from 'react'
import { connect } from 'react-redux';
import Aux from '../../hoc/Aux'
import Burger from '../../components/Burger/Burger'
import BurgerControls from '../../components/Burger/BuildControls/BuildControls'
import Modal from '../../components/UI/Model/Modal'
import OrderSummary from '../../components/Burger/OrderSummary/OrderSummary'
import axios from '../../axios-orders'
import Spinner from '../../components/UI/Spinner/Spinner'
import withErrorHandler from '../../components/withErrorHandler/withErrorHandler'


import * as actions from '../../store/actions/index'

class BurgerBuilder extends Component {
    state = {
       
        // ingredients:null ,
      
        purchasing: false,
       
    }
    componentDidMount(){
      this.props.onSetingridients()
    }
    updtePurchaseState(ingredients){
const sum =Object.keys(ingredients)
.map(igkey=>{
    return ingredients[igkey]
})
.reduce((sum,el) =>{
    return sum + el;
},0)
return sum >0

    }
    
   

    
    purchaseHandler = () => {
        if(this.props.isAuthenticated){
            this.setState({purchasing: true})
        }else{
            this.props.onAuthRedirectPath("/checkout")
            this.props.history.push('/auth')
        }
        
    }
    purchaseCancelHandaler = () => {
        this.setState({purchasing: false});
    }
    // purchaseContinueHandaler=()=>{
    //     // alert("Your are Continue?")
    //     this.setState({loading:true})
    //     const order = {
    //         ingredients: this.state.ingredients,
    //         price:this.state.totalPrice,
    //         customer:{
    //             name: " Osman goni",
    //             address: {
    //                 zipcode:'41351',
    //                 conutry: "Bangladesh"
    //             },
    //             email:'osmansufy@geostruc.com',

    //         },
    //         deliveryMethod: 'sslcommerz'
    //     }
    //     axios.post('/orders.json',order)
    //     .then(response=>{
    //         this.setState({loading:false,purchasing:false})
    //     })
    //     .catch(error=>{
    //         this.setState({loading:false,purchasing:false})
    //     })
    // }
    purchaseContinueHandler = () => {
        this.props.onInitPurchase();
        this.props.history.push('/checkout');
    }

    render (){
        
        const desableInfo={
            ...this.props.ings
        }

        for( let key in desableInfo){
            desableInfo[key] = desableInfo[key]<=0
        }
       
        
        let orderSummary = null

         let burger=this.props.error ? <p>Ingredients can't be loaded</p> :<Spinner />
         if(this.props.ings){
            burger= (<Aux>
                <Burger ingredients={this.props.ings} />
       <BurgerControls
       ingredientAdded={this.props.onIngredientAdded}
       ingredientRemoved={this.props.onIngredientRemoved}
        desable={desableInfo}
        purchasable={this.updtePurchaseState(this.props.ings)}
        price={this.props.price}
        ordered={this.purchaseHandler}
        isAuth={this.props.isAuthenticated}
       />
     
            </Aux>)
               orderSummary =   <OrderSummary 
               ingredients={this.props.ings}
               cancel={this.purchaseCancelHandaler}
               continue={this.purchaseContinueHandler}
               price={this.props.price}
                />
         }
         
        return (

        
<Aux>
<Modal 
show={this.state.purchasing}
modalClosed={this.purchaseCancelHandaler}
>
                  {orderSummary}
                </Modal>
                {burger}
    
</Aux>
        )
    }
}
const mapStateToProps = state => {
    return {
        ings: state.burgerBuilder.ingredients,
        price: state.burgerBuilder.totalPrice,
        error:state.burgerBuilder.error,
        isAuthenticated:state.auth.token !==null
       
    };
}

const mapDispatchToProps = dispatch => {
    return {
        onIngredientAdded: (ingName) => dispatch(actions.addIngridient(ingName)),
        onIngredientRemoved: (ingName) => dispatch(actions.removeIngridient(ingName)),
        onSetingridients:()=>dispatch(actions.initFetchIngridients()),
        onInitPurchase: () => dispatch(actions.purchaseInit()),
        onAuthRedirectPath:(path)=>dispatch(actions.setAuthRedirectPath(path))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withErrorHandler( BurgerBuilder, axios ));